import {Dispatch} from "redux";
import {UserAPI} from "../../DAL/API/api";
import {StateType} from "../Store/redux-store";
import {getfp} from "../Tools/FingerPrint";
import {setViewCountDataTC} from "./visitorCounterReducer";

type ActionType = SetDataAT

type UserType = {
    visitorIp: string
    visitorId: string
    visitorPlatform: string
    visitorDeviceMemory: number | undefined
    visitorProductSub: string | undefined
    visitorScreenResolution: Array<number> | undefined

}
export type UsersType = {
    users: UserType[]

}
let initialState: UsersType = {

    users: [
        /*  {ip:"",id:""}*/
    ]
}

export const usersReducer = (state: UsersType = initialState, action: ActionType): UsersType => {
    switch (action.type) {
        case SET_DATA: {
            return {
                ...state,

                users: [...state.users, ...action.payload.users]
            }
        }
        default:
            return state

    }
}
//--------------------------------------SET-DATA-AC-------------------------------
const SET_DATA = "usersReducer/SET_DATA"
type SetDataAT = {
    type: typeof SET_DATA
    payload: UsersType
}
export const setUsersDataAC = (payload: UsersType): SetDataAT => ({
    type: SET_DATA,
    payload: payload
})
//--------------------------------------SET-INITIALIZED-TC-------------------------------
export const setUsersDataTC = () => async (dispatch: Dispatch<any>, getState: () => StateType) => {
    /* let response = await UserAPI.getUsersInfo()
     dispatch(setUsersDataAC(response))*/


    let id = getState().currentFPUserInfo.visitorId
    let ip = getState().currentIpUserInfo.ipData.query
    let platform = getState().currentFPUserInfo.visitorPlatform
    let memory = getState().currentFPUserInfo.visitorDeviceMemory
    let productSub = getState().currentFPUserInfo.visitorProductSub
    let resolution = getState().currentFPUserInfo.visitorScreenResolution


    let filteredArray = getState().savedUsers.users.filter(el =>
        (el.visitorDeviceMemory === memory
            && el.visitorProductSub === productSub
            && el.visitorScreenResolution === resolution)
        || el.visitorIp === ip
        || el.visitorPlatform === platform
        || el.visitorId === id)

    if (filteredArray.length === 0) {
        let payload: UsersType = {
            users: [
                {
                    visitorId: id,
                    visitorIp: ip,
                    visitorPlatform: platform,
                    visitorDeviceMemory: memory,
                    visitorProductSub: productSub,
                    visitorScreenResolution: resolution
                }
            ]
        }

        dispatch(setUsersDataAC(payload))
        await UserAPI.writeInformation(getState().savedUsers)

        dispatch(setViewCountDataTC())
    }

}